<template>
  <div class="app-main">
    <!-- header begin -->
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <!-- header end -->
    <div class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs class="van-tabs" :active="active" swipeable animated sticky>
          <van-tab v-for="(tab,index) in tabs" :key="index" :title="tab.name">
            <jbrdb v-if="tab.dblx==='0'" ref="jbrdblball" @ItemClick="ItemClick" />
            <jbryb v-if="tab.dblx==='1'" ref="jbrdblball" @ItemClick1="ItemClick1" />
          </van-tab>
        </van-tabs>
      </van-sticky>
      <!-- <v-nav :swiper-send-index="swiperToNav" @changeTab="clickIndex" />
      <v-swiper :swiper-index="slideIndex" @slideTab="slideSendNav" /> -->
    </div>
    <FooterNav />

    <searchMeun @goSearchAction="searchXmxx($event)" @reSet="reSet">
      <van-field v-model="search.xmmc" label="待办名称" placeholder="请输入待办名称" input-align="right" clearable />
    </searchMeun>
  </div>
</template>

<script scoped>
import FooterNav from '@/components/footerNav/footerNav.vue'
import { Dialog } from 'vant'
import jbrdb from './dbsxIndex/jbrdb.vue'
import jbryb from './dbsxIndex/jbrdbls.vue'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'

import headerBar from '@/components/header/headerBar.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
export default {
  name: 'DbsxIndex',
  components: {
    jbrdb,
    jbryb,
    FooterNav,
    headerBar,
    searchMeun
  },
  mixins: [pageRouter],
  data() {
    return {
      slideIndex: 0, // 点击导航发送给swiper的值
      swiperToNav: 0, // swiper滑动下标值传给nav导航
      isRefresh: false,
      querys: {
        bt: '',
        cgfs: '',
        lb: ''
      },
      active: 0,
      headerData: {
        title: '待办事项',
        leftUrl: '/MhHome',
        leftText: ' ',
        showSearch: true
      },
      tabs: [
        { name: '我的待办', dblx: '0' },
        { name: '待办历史', dblx: '1' }
      ], // 0 待办   1：已办
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      search: {
        xmmc: ''
      },
      searchPick: {
        PickTitle: '',
        PickColumns: ''
      },
      searchCalendar: {
        calendarName: ''
      },
      pageName: ''
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      ddLogin: state => state.dd.ddType,
      refresh: state => state.jbrdbsx.refresh
    })
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 1)
    this.getXlsl()
  },
  methods: {
    reSet() {
      this.search.xmmc = ''
    },
    getXlsl() {
      this.$store.dispatch('jbrdbsx/getWdslAction', {
        userid: this.userInfo.id,
        apptype: 'APP'
      }).then(res => {
        this.$store.commit('router/SET_MsgCount', res)
      })
    },
    // 搜索点击事件
    searchXmxx(type) {
      console.log('-------------', type)
      this.$bus.$emit('showMeun', false)
      const query = {
        bt: this.search.xmmc
      }
      this.$bus.$emit('changeDbQuery', query) // bus触发事件
    },
    /* 点击事件 */
    ItemClick1(item) {
      if (item.status === 2) {
        this.$store.dispatch('jbrdbsx/getWdslAction', {
          userid: this.userInfo.id,
          apptype: 'APP'
        }).then(res => {
          this.$store.commit('router/SET_MsgCount', res)
        })
      }
      this.$store.dispatch('jbrdbsx/setTxbizid', item.bizId)
      var list = []
      this.$store.commit('router/SET_KEEP_ALIVE', list)
      this.$router.push({
        name: 'DbsxXxxx',
        params: { title: '待办详情' }
      })
    },
    /* 点击事件 */
    ItemClick(item) {
      if (item.status === 2) {
        this.$store.dispatch('jbrdbsx/getWdslAction', {
          userid: this.userInfo.id,
          apptype: 'APP'
        }).then(res => {
          this.$store.commit('router/SET_MsgCount', res)
        })
      }
      var list = []
      this.$store.commit('router/SET_KEEP_ALIVE', list)
      this.$store
        .dispatch('jbrdbsx/updateTxZtAction', {
          id: item.id
        })
        .then(res => {
          this.loading = false
          let txfs = item.txfs
          if (item.ta && item.ta !== '') {
            txfs = item.ta
          }
          if (txfs === 3) { // 流程处理
            this.$store.dispatch('jbrdbsx/setTxbizid', item.bizId)
            this.$router.push({
              name: 'DbsxXxxx',
              params: { title: '待办详情' }
            })
          } else if (txfs === 2) { // 功能中处理
            Dialog.alert({
              title: item.processName,
              message: ((item.txnr === item.processName) ? '' : item.txnr) + '\n该任务需前往PC端进行办理！'
            }).then(() => {
            })
          } else { // 普通提醒
            Dialog.confirm({
              message: item.txnr,
              confirmButtonText: '已阅'
            }).then(() => {
              this.$store.dispatch('jbrdbsx/dealTxActions', {
                ywbh: item.bizId
              }).then(res => {
                setTimeout(() => {
                  this.$store.dispatch('jbrdbsx/setRefresh', 'true')
                }, 2000)
              }).catch(() => {
              })
            }).catch(() => {
              // on cancel
            })
          }
        })
        .catch(() => {
          // this.loading = false
        })
    },
    // 点击导航菜单通过emit传给父亲然后父亲再传给子组件swiper组件
    clickIndex(index) {
      this.slideIndex = index
      // this.$store.commit('saveNavIndex', index)
    },
    // swiper滑动后下标通过emit传给父亲。父亲再传给子组件nav
    slideSendNav(index) {
      this.swiperToNav = index
      this.isRefresh = true
      // this.$store.commit('saveSwiperIndex', index)
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
