<template>
  <div>

    <common-list
      ref="commonlist"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div class="content">
        <div class="common-main box">
          <div class="common-list">
            <!-- :class="{ focus: ind === index }" -->
            <div
              v-for="(item, index) in list.items"
              :key="index"
              :class="item.status ===2 ? 'list':'list yd'"
              @click="onitemClick(index)"
            >
              <h3>{{ item.processName }}</h3>
              <div class="info">
                <span>
                  <i class="iconfont iconlianxiren" />
                  {{ item.preUsers }}
                </span>
                <span>
                  <i class="iconfont iconshijian" />
                  {{ item.showTime }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </common-list>
  </div>

</template>

<script land="script" scoped>
import constants from '../../../../common/js/constants.js'
import commonList from '../../../../components/list/commonList.vue'
import { mapState } from 'vuex'

export default {
  name: 'Jbrdball',
  // props: ['pageButton', 'pageTitle', 'pageImage', 'pageStyle'],
  components: {
    // xyScroll,
    // errorPage,
    commonList

  },
  data() {
    return {
      ind: null, // 点击事件
      querys: {
        bt: '',
        cgfs: '',
        lb: ''
      },
      hackReset: true,
      prizeLog: [],
      jbrdballPage: {
        pageStyles: 0, // 页面状态展示
        pageTitle: ''
      },
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      refresh: state => state.jbrdbsx.refresh
    })
  },
  watch: {
    refresh: function(old, newd) {
      if (old === 'true') {
        this.$store.dispatch('jbrdbsx/setRefresh', '')
        this.$refs.commonlist.onRefresh()
      }
    }
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.TimeDay = constants.TimeDay
    // 不会引起DOM变化的数据在此定义
    this.$bus.$on('changeDbQuery', (msg) => { // bus接收事件
      console.log('msg==============', msg)
      this.$refs.commonlist.onRefresh(msg)
    })
    this.getdbsxlist()
  },
  beforeDestroy() {
    this.$bus.$off('changeDbQuery')
  },
  methods: {
    /** 组件中返回的数据结构
     */
    getPrizeLog(data) {
      this.list.items = data
    },
    getdbsxlist() {
      this.list.postUrl = 'jbrdbsx/getWdybListAction'
      this.list.reqData = { userid: this.userInfo.id }
      this.list.dataName = 'executors'
      // this.list.noDataText = '暂无更多待办事项'
    },
    // 错误按钮
    // parentReceive(type) {
    //   this.getDbsxListData(this.page)
    // },
    onitemClick(index) {
      this.$store.dispatch('jbrdbsx/setDbsxlistItem', this.list.items[index])
      // if (this.list.items[index].readTime !== null) {
      // this.$router.push('/xxxx')
      // this.$emit('errorButton')
      this.ItemClick(this.list.items[index])
      // } else {
      //   this.getRead(this.list.items[index].id)
      // }
    },
    ItemClick(item) {
      this.$emit('ItemClick1', item)
      // this.$router.push({
      //   name: 'DbsxXxxx',
      //   params: { title: '待办详情' }
      // })
    },
    changeBgc: function(index) {
      this.ind = index
    }
    // 处理阅知事件
    // getRead(id) {
    //   this.getdbRead({
    //     id: id
    //   // eslint-disable-next-line no-unused-vars
    //   }).then(res => {
    //     // this.$router.push('/xxxx')
    //     this.ItemClick()
    //   // eslint-disable-next-line handle-callback-err
    //   })
    // }

  }

}
</script>

